body {
    background-color: #f5f5f5;
    width: 100vw;
    height: 100vh;
}

.App {
    text-align: center;
    font-family: 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
}

.row {
    display: flex;
}

.cell {
    display: inline-block;
    width: 7rem;
    height: 7rem;
    margin: .25rem;
    background-color: #f5f5f5;
    border: 1px solid floralwhite;
    font-size: 91px;
}

.gameContainer {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

.submitName-btn, .restart-btn, .resetAll-btn {
    width: 300px;
    height: 30px;
    border-radius: 5px;
    font-size: 20px;
    margin-left: 10px;
    margin-top: 10px;
    background-color: #b1bed5;
}

.submitName-btn:hover, .restart-btn:hover, .resetAll-btn:hover {
    border: 1px solid whitesmoke;
    transform: translateY(1px);
    background-color: #d0d8e5;
}

.restart-btn {
    margin-bottom: 20px;
}

.inputField {
    width: 300px;
    height: 29px;
    font-size: 20px;
    justify-self: end;
    align-self: end;
}

.app-container {
    padding: 15px;
    /*border: 1px solid rgba(115, 115, 115, 0.15);*/
    /*box-shadow: rgba(0, 0, 0) 2px 4px 8px;*/
    transition: all .3s ease-in-out;
    margin: 5%;
    background-color: white;
    min-height: 70vh;
}

.emoji-list {
    display: flex;
    flex-wrap: wrap;
    margin: 10px 10px 10px 17px;
}

.emoji-list > span {
    border: lightgray solid 1px;
    border-radius: 4px;
    margin: 9px;
    padding: 7px;
    font-size: 55px;
}

.emoji-list > *:hover {
    border: 1px solid whitesmoke;
    transform: translateY(1px);
    background: rgba(0, 0, 0, 0.1);
}

.nameInputs {
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr 1fr;
    align-items: center;
}

h3 {
    grid-column: 1 / span 2;
}

.boards-container {
    display: grid;
    grid-template-columns: 1fr auto;
}

.scoreContainer {
    display: block;
    height: 150px;
}

.showScore {
    background-color: #f5f5f5;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    width: 300px;
    /*align-items: start;*/
}

.showScore > h4 {
    margin-top: 20px;
    grid-column: 1 / span 2;
    grid-row: 1;
    align-items: center;
}

.showScore > div {
    grid-column: 1 ;
}

.showScore > span {
    grid-column: 2;
    margin-top: 16px;
}

.resetAll-btn {
    margin-left: 0;
}

.scoreBoard-emoji {
    font-size: 30px;
    margin-right: 10px;
}

.win-row,
.win-column,
.win-diagonal-left,
.win-diagonal-right {
    width: 120px;
    height: 20px;
    background-color: #ffaaa5;
    animation: blinking ease-in-out 1.2s infinite;
    /*transform: scale(0) translateZ(0);*/
    /*transition: transform ease-in-out 1.2s;*/
    z-index: 1000;
    position: absolute;
    display: inline-block;
    margin-top: 48px;
    margin-left: -105px;
}

.win-column {
    transform: rotate(90deg);
    width: 135px;
    margin-left: -112px;
}

.win-diagonal-left {
    transform: rotate(135deg);
    width: 170px;
    margin-left: -128px;
}

.win-diagonal-right {
    transform: rotate(-135deg);
    width: 170px;
    margin-left: -129px;
    margin-top: 45px;
}

@keyframes blinking{
    0%   {background-color: #ffaaa5;}
    20%  {background-color: #ffd3b6;}
    40%  {background-color: #dcedc1;}
    60% {background-color: #a8e6cf;}
    80%  {background-color: #dcedc1;}
    100%  {background-color: #ffd3b6;}
}
